import React from 'react';
import {profileAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const ADD_POST = 'ADD-POST';
export const addPost = (newText) => {
    return {
        type: ADD_POST,
        newText
    }
}
const SET_PROFILE = 'SET-PROFILE';
export const setProfile = (profile) => {
    return {
        type: SET_PROFILE,
        profile
    }
}
const IS_LOADING = 'IS_LOADING';
export const isLoading = (isLoadingStatus) => {
    return {
        type: IS_LOADING,
        isLoadingStatus
    }
}
const SET_STATUS = 'SET_STATUS';
export const setStatus = (status) => {
    return {
        type: SET_STATUS,
        status
    }
}
const DELETE_POST = 'DELETE_POST';
export const deletePost = (postId) => {
    return {
        type: DELETE_POST,
        postId
    }
}
const CHANGE_USER_PHOTO = 'CHANGE_USER_PHOTO';
export const changeUserPhoto = (photoFile) => {
    return {
        type: CHANGE_USER_PHOTO,
        photoFile
    }
}
let initialState = {
    posts: [
        {id: 1, message: 'salam', likes: 15},
        {id: 2, message: 'Eleyke salam', likes: 35}
    ],
    profile: [],
    isLoadingStatus: true,
    status: 'status no loaded yet',
    // photos:''
}

let profileReducer = (state = initialState, action) => {
    switch (action.type) {

        case SET_PROFILE: {
            return {
                ...state,
                profile: [action.profile]
            }
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case IS_LOADING: {
            return {
                ...state,
                isLoadingStatus: action.isLoadingStatus
            }
        }
        case ADD_POST: {
            let newPost = {
                id: 3,
                message: action.newText,
                likes: 0
            }
            return {
                ...state,
                posts: [...state.posts, newPost],
            }
        }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(post => post.id != action.postId)
            }
        case CHANGE_USER_PHOTO:
            return {
                ...state,
                profile: [...state.profile],
                photos: action.photoFile
            }
        default :
            return state;
    }
}
export const getProfile = (userId) => {
    return async (dispatch) => {
        dispatch(isLoading(true));
        let response = await profileAPI.getProfile(userId);
        // debugger
        dispatch(isLoading(false));
        dispatch(setProfile(response));
    }
}
export const getStatus = (userId) => {
    return async (dispatch) => {
        dispatch(isLoading(true));
        let response = await profileAPI.getStatus(userId);
        dispatch(isLoading(false));
        dispatch(setStatus(response));
    }
}
export const updateStatus = (status) => {
    return async (dispatch) => {
        dispatch(isLoading(true));
        let response = await profileAPI.updateStatus(status);
        dispatch(isLoading(false));

        if (response.resultCode === 0) {
            dispatch(setStatus(status))
        }
    }
}
export const uploadPhoto = (photoFile) => {
    return async (dispatch,getState) => {
        let userId=getState().auth.id;
        dispatch(isLoading(true));
        let response = await profileAPI.uploadUserPhote(photoFile);
        dispatch(isLoading(false));
        if (response.resultCode === 0) {
            dispatch(changeUserPhoto(response.data.photos))
            dispatch(getProfile(userId))
        }
    }
}
//
export const updateProfile = (data) => {
    return async (dispatch,getState) => {
        let userId=getState().auth.id;
        dispatch(isLoading(true));
        let response = await profileAPI.updateProfile(data);
        dispatch(isLoading(false));
        if (response.resultCode === 0) {
            dispatch(getProfile(userId))
        }else{
            let errorMessage=response.messages.map(m=><p>{m}</p>);
            dispatch(stopSubmit('profileEditForm', {_error: [errorMessage]}));
            return Promise.reject([errorMessage]);
        }
    }
}

export default profileReducer;

