import * as axios from "axios";

const instance = axios.create({
        baseURL: 'https://social-network.samuraijs.com/api/1.0/',
        withCredentials:true, //qeydiyyatdan kecibmi?
        headers: {
            'API-KEY': 'e7a0392c-1e96-45ae-a727-e10d75ccea3e'
        }
    }
);

export const usersAPI = {
    getUsers(currentPage = 1, perPage = 5) {
        return instance.get(`users?page=${currentPage}&count=${perPage}`, {
            // withCredentials:true
        })
            .then(response => {
                    return response.data;
                }
            )
    }
}

export const authAPI = {
    auth() {
        return instance.get(`/auth/me`
        ).then(response => response.data)
    },
    login(email, password, rememberMe=false){
        return instance.post(`auth/login`,{
            email, password, rememberMe

        } ).then(response=>response.data)
    },
    logOut(){
        return instance.delete(`auth/login`).then(response=>response.data)
    }
}

export const profileAPI = {
    getProfile(userId = 8111) {
        return instance.get(`/profile/${userId}`).then(response => response.data)
    },
    getStatus(userId = 8111) {
        return instance.get(`/profile/status/${userId}`).then(response => response.data)
    },
    updateStatus(status) {
        return instance.put(`/profile/status/`, {status: status}).then(response => response.data)
    },
    uploadUserPhote(imageFile){
        let formData=new FormData();
        formData.append('image',imageFile);
        return instance.put(`/profile/photo`,formData,{header:{
            'Content-type':'multipart/form-data'
            }}).then(response=>response.data);
    },
    updateProfile(data){

        return instance.put(`/profile`,data).then(response=>response.data)
    }

}

export const followAPI = {
    follow(userId) {
        return instance.post(`/follow/${userId}`, {
            withCredintals: true
        })
            .then(response => response.data)
    },
    unfollow(userId) {
        return instance.delete(`/follow/${userId}`, {
            withCredintals: true
        })
            .then(response => response.data)
    }
}
