import React from 'react';
import p from './Post.module.css';

const Post = (props) => {
    let img='https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQd0r25XODQ5SftBiGJzxDRVzMw6B19B-ZCcM29kpo3c3Zpv3Nphd8igSxpbA&usqp=CAU';
  return (
    <div className={p.item}>
      <img src={img} alt="profil" />
      {props.message}
      <div>
      <span>like -{props.likes}</span>
      </div>
    </div>
  );
}
export default Post;
