import React, {Fragment, useEffect, useState} from 'react';
import p from './ProfileInfo.module.css';
import ShowLoadingAnimation from "../../common/ShowLoadingAnimation/ShowLoadingAnimation";
import video from "../../assets/video-full.mp4"
import ProfileStatusHook from "./ProfileStatusHook";
import imageLogo from "./../../assets/user-default-logo.png"
import {Form, reduxForm} from "redux-form";
import {createField, Input, Textarea} from "../../common/FormControls/FormControls";
import {required} from "../../../utils/validators/validators";

const ProfileInfo = ({profile, updateProfile, isLoadingStatus, status, updateStatus, isOwnerMe, uploadPhoto,getProfile}) => {
    let [editMode, setEditMode] = useState(false);

    const ProfileEditForm = ({handleSubmit, error}) => {
        return <Form onSubmit={handleSubmit} >
            <div>
                <button>Save Changes</button>
            </div>
            {error && <div>{error}</div>}
            {createField('Full Name', 'fullName', Input, [required], {}, '')}
            {createField('About Me', 'aboutMe', Input, [required], {}, '')}
            {createField('LookingForAJob', 'lookingForAJob', Input, [], {type: 'checkbox'}, 'Looking for Job')}
            {createField('LookingForAJobDescription', 'lookingForAJobDescription', Textarea,[required])}
            <p>Contacts :</p>
            {Object.keys(profile.contacts)
                .map(key =><b key={key}>{key} :{ createField( key , 'contacts.'+key , Input, [], {})}</b>)}
        </Form>
    }
    const WithProfileEditForm = reduxForm({
        form: 'profileEditForm',
        initialValues:profile,
        destroyOnUnmount:false,
        enableReinitialize:true
    })(ProfileEditForm)

    const ProfileDataInfo = ({userId, fullName, aboutMe, lookingForAJob, lookingForAJobDescription, contacts}) => {
        return <div key={userId}>
            <div className={p.descriptionText}>
                <p><b>Full Name :</b> {fullName}</p>
                <p><b>About Me :</b> {aboutMe}</p>
                <p><b>LookingForAJob :</b>{lookingForAJob ? ' yes' : ' no'}</p>
                <p><b>LookingForAJobDescription :</b> {lookingForAJobDescription}</p>
                <Contacts {...contacts}/>
            </div>
        </div>
    }
    const Contacts = (props) => {
        return <div><details><summary>Contacts</summary>
            {Object.keys(props).map(key => <option key={key}>{key} : {props[key]}</option>)}
        </details>
        </div>

    }
    const onUserPhotoChange = (event) => {
        if (event.target.files.length) {
            uploadPhoto(event.target.files[0]);
        }
    }
    const UserPhoto = ({photos}) => {
        return <div className={p.logo}>
            <label htmlFor='newLogoUpload'>
                <img src={photos.small ? photos.small : imageLogo}
                     alt="click to change user photo"/>
            </label>
            {isOwnerMe && <input id='newLogoUpload' type='file'
                                 className={p.hiddenInputFile}
                                 onChange={onUserPhotoChange}/>}
        </div>
    }

    const onChangeProfileInfo = (data) => {
        let promise=updateProfile(data);
            promise.then(()=>{
            setEditMode(false)
        })
    }
    return (
        <div key={profile.userId}>
            <div className={p.content__img}>
                <video loop={true} autoPlay={true} >
                    <source src={video} type="video/mp4"/>
                </video>
            </div>
            {(isLoadingStatus) ? <ShowLoadingAnimation/> : ''}
            {!editMode && <div className={p.description}>
                <UserPhoto  {...profile}/>
                <ProfileDataInfo {...profile}/>
                {isOwnerMe && <button onClick={() => {
                    setEditMode(true)}}>Edit Mode</button>}
            </div>}
            {editMode && <WithProfileEditForm
                onSubmit={onChangeProfileInfo} />}
            <ProfileStatusHook status={status} updateStatus={updateStatus}/>
        </div>
    )
}
export default ProfileInfo;
