import React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {getProfile, getStatus, updateStatus, addPost, uploadPhoto, updateProfile} from "../../redux/profileReducer";
import {withRouter} from "react-router-dom";
import {WithAuthRedirect} from "../../hoc/WithAuthRedirect";
import {compose} from "redux";

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    isLoadingStatus: state.profilePage.isLoadingStatus,
    status:state.profilePage.status,
    loggedUserId:state.auth.userId,
    isAuth:state.auth.isAuth,
    photos:state.profilePage.photos
})
class ProfileContainer extends React.Component {
    componentDidMount() {
        let userId = this.props.match.params.userId;  //userin- id-sini elde edirik
        this.props.getProfile(userId);
        this.props.getStatus(userId);
    }
    componentDidUpdate(prevProps, prevState) {
        if(prevProps.match.params.userId!=this.props.match.params.userId) {
            let userId = this.props.match.params.userId;  //userin- id-sini elde edirik
            this.props.getProfile(userId);
            this.props.getStatus(userId);
        }
    }
    render() {
        return (<div>
            <Profile {...this.props}
                     isOwnerMe={!this.props.match.params.userId}
                     profile={this.props.profile}
                     status={this.props.status}
                     updateStatus={this.props.updateStatus}
                     uploadPhoto={this.props.uploadPhoto}
                     getProfile={this.props.getProfile}
                     updateProfile={this.props.updateProfile}
            />
        </div>)
    }
}
export default compose(
    connect(mapStateToProps, {addPost,getProfile,getStatus,updateStatus,uploadPhoto,updateProfile}),
    withRouter,
    WithAuthRedirect,
)(ProfileContainer)


